  .start-page .start-page__desc-box {
    margin: 0;
  }
  .why-us__container {
    justify-content: center;
  }
  .why-us__desc-wrap {
    max-width: 100%;
  }
  .why-us__desc-box {
    text-align: center;
  }
  .start-page__desc-box {
    margin: 0;
    width: 70%;
  }
  .why-us__card:first-child {
    width: 100%;
  }
  .why-us__card-img {
    width: 100%;
    height: 100%;
  }
  .why-us__card,
  .why-us__card:not(:last-child) {
    margin-bottom: 10px;
    margin-right: 10px;
  }
  .callback::after {
    content: none;
  }
  section .callback__desc-box {
    max-width: 100%;
    margin-top: 34px;
  }
}
@media screen and (max-width: 480px) {
  .callback__form-wrapper {
    padding-left: 0;
    width: 100%;
    display: flex;
    align-items: center;
  }
  .callback__form {
    width: 100%;
  }
  .callback__form input,
  .callback__form textarea {
    width: 100%;
  }
}
